
/* Services Carousel Section */
.services-carousel-section {
  height: 100vh;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  overflow: hidden;
  position: relative;
  margin-top: -2em; /* Move up to account for CardNav top positioning */
}

.carousel-container {
  position: relative;
  width: 100%;
  height: 100vh;
  min-height: 100vh;
  overflow: hidden;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
}


.carousel-track {
  display: flex;
  height: 100%;
  transition: transform 0.8s cubic-bezier(0.4, 0, 0.2, 1);
}

.carousel-slide {
  min-width: 100%;
  height: 100%;
  position: relative;
}

.banner-container {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.banner-container::before,
.banner-container::after {
  content: '';
  position: absolute;
  left: -20px;
  right: -20px;
  height: 40%;
  z-index: 1;
  pointer-events: none;
  background-image: inherit;
  background-size: cover;
  background-position: center;
}

.banner-container::before {
  top: -30px;
  filter: blur(60px) saturate(2) brightness(1.3);
  mask: linear-gradient(180deg, rgba(0,0,0,1) 0%, rgba(0,0,0,0.9) 40%, rgba(0,0,0,0.5) 70%, transparent 100%);
  animation: topBlurFlow 12s ease-in-out infinite;
}

.banner-container::after {
  bottom: -30px;
  filter: blur(60px) saturate(2) brightness(1.3);
  mask: linear-gradient(0deg, rgba(0,0,0,1) 0%, rgba(0,0,0,0.9) 40%, rgba(0,0,0,0.5) 70%, transparent 100%);
  animation: bottomBlurFlow 15s ease-in-out infinite reverse;
}

@keyframes topBlurFlow {
  0%, 100% {
    filter: blur(60px) saturate(2) brightness(1.3);
    transform: scale(1.1) translateX(0px);
  }
  25% {
    filter: blur(80px) saturate(2.5) brightness(1.5);
    transform: scale(1.15) translateX(10px);
  }
  50% {
    filter: blur(70px) saturate(2.2) brightness(1.4);
    transform: scale(1.12) translateX(-5px);
  }
  75% {
    filter: blur(90px) saturate(2.8) brightness(1.6);
    transform: scale(1.18) translateX(8px);
  }
}

@keyframes bottomBlurFlow {
  0%, 100% {
    filter: blur(60px) saturate(2) brightness(1.3);
    transform: scale(1.1) translateX(0px);
  }
  33% {
    filter: blur(85px) saturate(2.4) brightness(1.5);
    transform: scale(1.14) translateX(-8px);
  }
  66% {
    filter: blur(75px) saturate(2.6) brightness(1.4);
    transform: scale(1.16) translateX(12px);
  }
}

.service-banner {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  transition: transform 0.3s ease;
}


.banner-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(
    135deg,
    rgba(0, 0, 0, 0.7) 0%,
    rgba(0, 0, 0, 0.3) 50%,
    transparent 100%
  );
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 0 5%;
  z-index: 3;
}

.banner-content {
  max-width: 500px;
  color: white;
  text-align: left;
}

.service-title {
  font-size: 4rem;
  font-weight: 900;
  margin: 0 0 1rem 0;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  letter-spacing: -2px;
  line-height: 0.9;
}

.service-description {
  font-size: 1.4rem;
  font-weight: 300;
  margin: 0;
  opacity: 0.95;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
  line-height: 1.4;
}

/* Carousel Controls */
.carousel-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(255, 255, 255, 0.2);
  border: none;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  color: white;
  font-size: 24px;
  cursor: pointer;
  transition: all 0.3s ease;
  z-index: 10;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.carousel-btn:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: translateY(-50%) scale(1.1);
}

.carousel-btn.prev {
  left: 30px;
}

.carousel-btn.next {
  right: 30px;
}

.carousel-btn span {
  display: block;
  font-weight: bold;
}

/* Carousel Indicators */
.carousel-indicators {
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 15px;
  z-index: 10;
}

.indicator {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: 2px solid rgba(255, 255, 255, 0.5);
  background: transparent;
  cursor: pointer;
  transition: all 0.3s ease;
}

.indicator.active {
  background: white;
  border-color: white;
  transform: scale(1.2);
}

.indicator:hover {
  border-color: white;
  transform: scale(1.1);
}

/* Service Details Section */
.service-details {
  padding: 120px 0;
  background: linear-gradient(180deg, #f8f9fa 0%, #ffffff 100%);
}

.service-features {
  margin-bottom: 80px;
}

.service-features h2 {
  font-size: 2.5rem;
  font-weight: 700;
  text-align: center;
  margin-bottom: 50px;
  color: #333;
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 25px;
  max-width: 1000px;
  margin: 0 auto;
}

.feature-item {
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 20px;
  background: white;
  border-radius: 12px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.08);
  transition: all 0.3s ease;
}

.feature-item:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.12);
}

.feature-icon {
  flex-shrink: 0;
  width: 24px;
  height: 24px;
  background: #4CAF50;
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  font-weight: bold;
}

.feature-text {
  font-size: 1.1rem;
  color: #555;
  line-height: 1.4;
}

/* CTA Section */
.cta-section {
  text-align: center;
  max-width: 600px;
  margin: 0 auto;
}

.cta-section h2 {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 20px;
  color: #333;
}

.cta-section p {
  font-size: 1.2rem;
  color: #666;
  margin-bottom: 40px;
  line-height: 1.6;
}

.cta-button {
  display: inline-block;
  padding: 18px 40px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  text-decoration: none;
  border-radius: 50px;
  font-size: 1.1rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
  transition: all 0.3s ease;
  box-shadow: 0 8px 25px rgba(102, 126, 234, 0.3);
}

.cta-button:hover {
  transform: translateY(-3px);
  box-shadow: 0 12px 35px rgba(102, 126, 234, 0.4);
  color: white;
  text-decoration: none;
}

/* Responsive Design */
@media (max-width: 768px) {
  .services-carousel-section {
    min-height: 80vh;
    padding: 20px;
  }

  .carousel-container {
    height: 65vh;
    min-height: 480px;
  }

  .service-title {
    font-size: 2.5rem;
  }

  .service-description {
    font-size: 1.1rem;
  }

  .banner-overlay {
    padding: 0 20px;
  }

  .carousel-btn {
    width: 50px;
    height: 50px;
    font-size: 20px;
  }

  .carousel-btn.prev {
    left: 15px;
  }

  .carousel-btn.next {
    right: 15px;
  }

  .features-grid {
    grid-template-columns: 1fr;
    gap: 15px;
  }

  .service-details {
    padding: 80px 0;
  }

  .service-features h2,
  .cta-section h2 {
    font-size: 2rem;
  }

  .ambient-lights {
    height: 150px;
  }
}

@media (max-width: 480px) {
  .service-title {
    font-size: 2rem;
  }

  .service-description {
    font-size: 1rem;
  }

  .carousel-container {
    height: 55vh;
    min-height: 420px;
  }

  .feature-item {
    padding: 15px;
  }

  .cta-button {
    padding: 15px 30px;
    font-size: 1rem;
  }
}